import React, { Component } from 'react';
import classnames from 'classnames';
import { NavLink } from 'react-router-dom';

class LoginForm extends Component {

    constructor(props) {
        super(props);
        this.state = {
            email: '',
            password: '',
            submit: false,
            emailValiMsg: '',
            passwordValidMsg: ''
        };
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.logined) {
            this.props.history.push('/');
        }
    }

    // 
    handleChange = (e) => {
        let newState = {
            [e.target.name]: e.target.value
        };
        this.setState(newState);
    }

    // 
    handleSubmit = (e) => {
        e.preventDefault();
        this.setState({ submit: true });

        let valid = false;
        // 
        // 
        let emailRegex = /\S+?@\S+?\.\S+$/;
        if (!this.state.email) this.setState({ emailValiMsg: 'email' });
        else if (!this.state.email.match(emailRegex)) this.setState({ emailValiMsg: 'email' });
        else {
            this.setState({ emailValiMsg: '' });
            valid = true;
        }

        // 
        if (!this.state.password) {
            this.setState({ passwordValidMsg: 'password' });
            valid = false;
        }
        else {
            this.setState({ passwordValidMsg: '' });
            valid = valid && true;
        }

        if (valid) {
            this.props.doLogin(this.state.email, this.state.password);
        }
    }

    render() {
        return (
            <div id='login-form'>
                <h3 className='mt-4 mb-4 font-weight-light border-bottom p-2'></h3>
                <div className='row'>
                    <div className='col-md-4 col-sm-12'>
                        <form onSubmit={this.handleSubmit}>
                            <div className="form-group">
                                <label htmlFor='email'></label>
                                <input className={classnames({
                                    'form-control': true,
                                    'is-invalid': !!this.state.emailValiMsg,
                                })} id='email' name='email' type='text' placeholder='' onChange={this.handleChange} />
                                <small className='invalid-feedback'>{this.state.emailValiMsg}</small>
                            </div>
                            <div className='form-group'>
                                <label htmlFor='password'></label>
                                <input className={classnames({
                                    'form-control': true,
                                    'is-invalid': !!this.state.passwordValidMsg,
                                })} id='password' name='password' type='password' placeholder='' onChange={this.handleChange} />
                                <span className="invalid-feedback">{this.state.passwordValidMsg}</span>
                            </div>
                            <button className="btn btn-info" type="submit"></button>
                        </form>
                    </div>
                    <div className="col-md">
                        <div className='mt-4'>
                            <p><NavLink to='register'></NavLink></p>
                            <p><NavLink to='/'></NavLink></p>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default LoginForm;


