import * as mUrl from '../url';
import { startLoading, endLoading } from './loading';
import { showAlert } from './alert';

// 
export const requestLogin = () => ({
    type: 'REQUEST_LOGIN',
});

// 
export const loginSuccess = (email, data) => ({
    type: 'LOGIN_SUCCESS',
    logined: true,
    email,
    ...data
});

// 
export const loginFail = (code, msg) => ({
    type: 'LOGIN_FAIL',
    code,
    msg
});

// 
export const requestRegister = () => ({
    type: 'REQUEST_REGISTER',
});

// 
export const registerSuccess = (email, data) => ({
    type: 'REGISTER_SUCCESS',
    email,
    ...data
});

// 
export const registerFail = (code, msg) => ({
    type: 'REGISTER_FAIL',
    code,
    msg
});

// 
export const requestLogout = () => ({
    type: 'REQUEST_LOGOUT'
});

// 
export const logoutSuccess = () => ({
    type: "LOGOUT_SUCCESS"
});

// 
export const logoutFail = () => ({
    type: "LOGOUT_FAIL"
});

// 
export const requestCheckEmail = () => ({
    type: 'REQUEST_CHECK_EMAIL'
});

// 
export const emailValid = () => ({
    type: 'EMAIL_VALID'
});

// 
export const emailInvalid = () => ({
    type: 'EMAIL_INVALID'
});

export const requestUserInfo = () => ({
    type: 'REQUEST_USER_INFO'
});

export const getUserInfoSuccess = (data) => ({
    type: 'GET_USER_INFO_SUCCESS',
    ...data
});

export const getUserInfoFail = () => ({
    type: 'GET_USER_INFO_FAIL'
});

// action
export const fetchLogin = (email, password) => {
    return (dispatch) => {
        dispatch(requestLogin());
        fetch(mUrl.login, {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
            },
            body: `email=${email}&password=${password}`,
            credentials: 'include'
        }).then(response => response.ok && response.json())
            .then(json => {
                if (!json) return;
                if (json.code == 1) {
                    dispatch(loginSuccess(email, json.data));
                    dispatch(endLoading());
                    dispatch(showAlert(json.msg, 2000, "success"));
                }
                else {
                    dispatch(loginFail(json.code, json.msg));
                    dispatch(showAlert(json.msg, 2000, "danger"));
                }
            }).catch(response => { return; });
    };
};

// action
export const fetchLogout = () => {
    return (dispatch) => {
        dispatch(requestLogout());
        fetch(mUrl.logout, {
            method: 'GET',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
            },
            credentials: 'include'
        }).then(response => response.ok && response.json())
            .then(json => {
                if (!json) return;
                if (json.code == 1) {
                    dispatch(logoutSuccess());
                    dispatch(showAlert(json.msg, 2000, "success"));
                }
                else {
                    dispatch(logoutFail());
                    dispatch(showAlert(json.msg, 2000, "danger"));
                }
            }).catch(response => {
                return;
            });
    };
};

// action
export const fetchRegister = (nickname, email, password) => {
    return (dispatch) => {
        dispatch(requestRegister());
        fetch(mUrl.register, {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
            },
            body: `nickname=${nickname}&email=${email}&password=${password}`,
            credentials: 'include'
        }).then(response => response.ok && response.json())
            .then(json => {
                if (!json) return;
                if (json.code == 1) {
                    dispatch(registerSuccess(email, json.data));
                    dispatch(showAlert(json.msg, 2000, "success"));
                }
                else {
                    dispatch(registerFail(json.code, json.msg));
                    dispatch(showAlert(json.msg, 2000, "danger"));
                }
            }).catch(response => { return; });
    };
};

export const fetchUserInfo = () => {
    return (dispatch) => {
        dispatch(requestUserInfo());
        fetch(mUrl.people, {
            method: 'GET',
            headers: {
                'Accept': 'application/json',
            },
            credentials: 'include'
        }).then(response => response.ok && response.json())
            .then(json => {
                if (!json) return;
                if (json.code == 1) {
                    dispatch(getUserInfoSuccess(json.data));
                }
                else dispatch(getUserInfoFail());
            }).catch(response => { return; });

    };
};


