import React, { Component } from 'react';
import classnames from 'classnames';
import { NavLink } from 'react-router-dom';

class RegisterForm extends Component {

    constructor(props) {
        super(props);
        this.state = {
            email: '',
            password: '',
            nickname: '',
            submit: false,
            emailValidMsg: '',
            passwordValidMsg: '',
            nicknameValidMsg: ''
        };
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.logined) {
            this.props.history.push('/');
        }
    }

    // 
    handleChange = (e) => {
        let newState = {
            [e.target.name]: e.target.value
        };
        this.setState(newState);
    }

    // 
    handleSubmit = (e) => {
        e.preventDefault();
        this.setState({ submit: true });

        let valid = false;
        // 
        // 
        let emailRegex = /\S+?@\S+?\.\S+$/;
        if (!this.state.email.trim()) this.setState({ emailValidMsg: 'email' });
        else if (!this.state.email.match(emailRegex)) this.setState({ emailValidMsg: 'email' });
        else {
            this.setState({ emailValidMsg: '' });
            valid = true;
        }

        // 
        if (!this.state.password) {
            this.setState({ passwordValidMsg: 'password' });
            valid = false;
        }
        else {
            this.setState({ passwordValidMsg: '' });
        }

        // 
        if (!this.state.nickname) {
            this.setState({ nicknameValidMsg: '' });
            valid = false;
        }
        else {
            this.setState({ passwordValidMsg: '' });
        }

        if (valid) {
            // TODO: 
            this.props.doRegister(this.state.nickname, this.state.email, this.state.password);
        }
    }

    render() {
        return (
            <div id='register-form'>
                <h3 className='mt-4 mb-4 font-weight-light border-bottom p-2'></h3>
                <div className='row'>
                    <div className='col-md-4 col-sm-12'>
                        <form onSubmit={this.handleSubmit}>
                            <div className="form-group">
                                <label htmlFor="nickname"></label>
                                <input name='nickname' id='nickname' className={classnames({
                                    'form-control': true,
                                    'is-invalid': !!this.state.nicknameValidMsg
                                })} type="text" placeholder='' onChange={this.handleChange} />
                                <small className='invalid-feedback'>{this.state.nicknameValidMsg}</small>
                            </div>
                            <div className="form-group">
                                <label htmlFor='email'></label>
                                <input className={classnames({
                                    'form-control': true,
                                    'is-invalid': !!this.state.emailValidMsg,
                                })} id='email' name='email' type='text' placeholder='' onChange={this.handleChange} />
                                <small className='invalid-feedback'>{this.state.emailValidMsg}</small>
                            </div>
                            <div className='form-group'>
                                <label htmlFor='password'></label>
                                <input className={classnames({
                                    'form-control': true,
                                    'is-invalid': !!this.state.passwordValidMsg,
                                })} id='password' name='password' type='password' placeholder='' onChange={this.handleChange} />
                                <span className="invalid-feedback">{this.state.passwordValidMsg}</span>
                            </div>
                            <button className="btn btn-info" type="submit"></button>
                        </form>
                    </div>
                    <div className="col-md-4 col-sm-12">
                        <div className='mt-4'>
                            <h4></h4>
                            <p>Lorem ipsum, dolor sit amet consectetur adipisicing elit. Nihil tempora voluptates ratione, alias quae odit ab! Ea necessitatibus omnis assumenda, recusandae nihil quod nemo nisi nesciunt beatae maiores distinctio natus?</p>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default RegisterForm;


